#viewer {
    h1 {
        font-size: 1.25rem!important;
        font-weight: 600!important;
        letter-spacing: -0.026rem!important;
        line-height: 2rem!important;
        margin: 0!important;
        padding: 0!important;
        display: flex;
        align-items: center;
        color: $white!important;
    }
    h2 {
        font-size: 0.75rem!important;
        font-weight: 500!important;
        letter-spacing: -0.016rem!important;
        margin: 0!important;
        padding: 0!important;
        display: flex;
        align-items: center;
        line-height: 0.938rem!important;
    }
    h3 {
        font-size: 0.75rem!important;
        font-weight: 700!important;
        letter-spacing: -0.016rem!important;
        line-height: .938rem!important;
        margin: 0!important;
        padding: 0!important;
        span {
            color: $v!important;
        }
    }
    h5 {
        font-size: 0.75rem!important;
        font-weight: 700!important;
        letter-spacing: -0.014rem!important;
        line-height: 1.125!important;
        margin: 0!important;
        padding: 0!important;
    }
    h6 {
        font-size: 0.688rem!important;
        font-weight: 500!important;
        letter-spacing: -0.016rem!important;
        line-height: 0.938rem!important;
        margin: 0!important;
        padding: 0!important;
    }
    p {
        font-size: 0.938rem!important;
        font-weight: 800!important;
        letter-spacing: -0.019rem!important;
        line-height: 1.25rem!important;
        margin: 0!important;
        padding: 0!important;
    }
}
    .back {
        &-container {
            display: flex;
            width: 5.125rem!important;
            justify-content: space-between;
            align-items: center;
            z-index: 1
        }
        &-container-filler {
            position: absolute;
            height: 3rem!important;
            width: 6rem!important;
            left: -.5rem;
            top: -.8rem;
            z-index: 0;
        }
        &-arrow {
            background-image: url(../assets/icon-arrow-left.svg);
            width: 0.438rem!important;
            height: 0.625rem!important;
            transition: .5s;
        }
    }
    .viewer {
        &-container {
            margin: 2rem 1.5rem 3.5rem 1.5rem!important;
        }
    }
    .viewer-status {
        &-outer-container {
            height: 5.688rem!important;
            margin-top: 2rem!important;
            border-radius: 0.5rem!important;
            border: 0.063rem $trans solid;
            z-index: 0!important;
        }
        &-inner-container {
            padding: 1.5rem 1.5rem 1.688rem !important;
        }
    }
    .viewer-main {
            &-outer-container {
                height: auto!important;
                margin-top: 1rem!important;
                border-radius: 0.5rem!important;
                border: 0.063rem $trans solid;
                z-index: 0!important;
            }
            &-inner-container {
                padding: 1.5rem!important;
                height: 100%!important
            }
            &-left-column-container {
                width: 8.563rem!important;
            }
            &-right-column-container {
                width: 8.75rem!important;
            }
            &-id-subscription-container {
                height: 2.188rem!important;
            }
            &-address-container {
                margin-top: 2.125rem!important;
                height: 4.313rem!important
            }
            &-invoice-container {
                margin-top: 2.375rem!important;
                height: 2.938rem!important
            }
            &-due-container {
                margin-top: 2.375rem!important;
                height: 2.938rem!important
            }
            &-bill-container {
                margin-top: 2.375rem!important;
                height: 2.938rem!important
            }
            &-bill-address-outer-container {
                margin-top: 1rem!important;
            }
            &-bill-address-inner-container {
                height: 4.375rem!important
            }
            &-sent-outer-container {
                margin-top: 2.375rem!important;  
                height: 2.813rem!important;        
            }
            &-sent-inner-container {
                height: 2.813rem!important;
            }
            &-pricing-outer-container {
                margin-top: 2.563rem!important;
                width: 100%!important;
                border-radius: 0.5rem!important;
            }
            &-pricing-inner-container {
                padding: 1.5rem 1.5rem 0rem 1.5rem!important;
                height: 100%!important;
            }
            &-pricing-item-container {
                width: 100%!important;
                height: 2.375rem!important;
                margin-bottom: 1.5rem!important;
            }
            &-pricing-grand-total-outer-container {
                height: 5rem!important;
                border-bottom-right-radius: 0.5rem!important;
                border-bottom-left-radius: 0.5rem!important;
            }
            &-pricing-grand-total-inner-container {
                padding: 1.813rem 1.5rem 2.125rem!important;
                h6 {
                    color: $white!important;
                }
            }
    }
    .viewer-footer {
        &-outer-container {
            height: 5.688rem!important;
            width: 100%!important;
            margin: 0!important;
        }
        &-inner-container {
            padding: 1.313rem 1.5rem 1.375rem 1.5rem!important;
            height: 100%!important;
        }
        &-button-gap {
            width: 0.5rem!important;
        }
        &-edit-button-container {
            border-radius: 1.513rem!important;
            width: 4.563rem!important;
            height: 100%!important;
        }
        &-delete-button-container {
            border-radius: 1.513rem!important;
            width: 5.563rem!important;
            background-color: $i;
            height: 100%!important;
            h3 {
                color: $white;
            }
        }
        &-paid-button-container {
            border-radius: 1.513rem!important;
            width: 9.313rem!important;
            background-color: $n;
            height: 100%!important;
            h3 {
                color: $white;
            }
        }
    }
    .viewer-transition {
        animation-name: viewer-trans-in;
        animation-duration: .5s;
        animation-fill-mode: forwards;
    }
    
    @keyframes viewer-trans-in {
        from {padding-top: 5rem; opacity: 0}
        to {padding-top: 0rem; opacity: 1}
    }